.Footer {
    position: absolute;
    width: 100%;
    height: 40vh;
    background-color: #FCC700;
    border-top: 3vh solid #000000;
    overflow: hidden;
}

.Footer__content {
    height: 100%;
    margin-left: 10%;
    margin-right: 10%;
    justify-content: space-between;
    display: flex;

}

.Footer__contener {
    padding: 10px 10px 0 10px;
    width: 20%;
}

.Footer__contener h2 {
    font-size: 3.5vh;
}

.Footer__contener h3 {

